import React, { useState, useEffect } from 'react';

import Box, { BoxProps } from '@mui/joy/Box';
import Tooltip from '@mui/joy/Tooltip';
import IconButton from '@mui/joy/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import GitHubIcon from '@mui/icons-material/GitHub';
import Container from '../components/Container';

interface AppHeaderProps extends BoxProps {
  title: string;
  isArticle: boolean;
  hasSidebar: boolean;
  handleDrawerToggle: () => void;
  sx?: any;
}

const HEIGHT = 60;
const FONT_SIZE = '1.105rem';

const GITHUB_REPO = 'https://github.com/memset0/blog';

const headerStyles = {
  p: 2,
  gap: 2,

  display: 'flex',
  flexDirection: 'row',
  gridColumn: '1 / -1',

  paddingLeft: 0,
  paddingRight: 0,

  alignItems: 'center',
  justifyContent: 'space-between',

  position: 'sticky',
  top: 0,
  zIndex: 'var(--joy-zIndex-appBar)',

  backgroundColor: 'rgba(255,255,255,0.84)',
  backdropFilter: 'blur(8px)',

  borderBottom: '1px solid',
  borderColor: 'divider',
};

export default (function Header(props) {
  const { isArticle, hasSidebar, handleDrawerToggle } = props;
  const [isTop, setIsTop] = useState(true);

  const handleScroll = () => {
    const { scrollTop, clientHeight, scrollHeight } = document.documentElement;

    if (scrollTop > HEIGHT * 2) {
      setIsTop(false);
    } else {
      setIsTop(true);
    }
  };

  useEffect(() => {
    handleScroll();
    document.addEventListener('scroll', handleScroll);
    return () => {
      document.removeEventListener('scroll', handleScroll);
    };
  });

  return (
    <Box
      component="header"
      className="header"
      sx={[
        headerStyles,
        {
          height: HEIGHT,
          maxHeight: HEIGHT,
        },
        !isTop && {
          boxShadow: 'var(--joy-shadows-2)',
        },
        ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
      ]}
    >
      <Container sx={{ display: 'flex' }}>
        <Box sx={{ display: 'inline-block', mr: 1, marginTop: `${(HEIGHT - 40) / 2}px` }}>
          <IconButton sx={{ display: { xs: 'flex', md: 'none' } }} component="button" color="primary" onClick={handleDrawerToggle} data-ga-event-category="header" data-ga-event-action="github">
            <MenuIcon fontSize="small" />
          </IconButton>
          <Box sx={{ display: { xs: 'none', md: 'block' }, width: '40px', height: '40px' }}></Box>
        </Box>
        <Box
          sx={{
            display: 'inline-block',
            fontSize: FONT_SIZE,
            width: 'calc(100% - 40px)',
            height: HEIGHT,
            lineHeight: HEIGHT + 'px',
            textAlign: 'center',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          {props.title}
        </Box>
        <Box sx={{ display: { xs: 'none', sm: 'inline-block' }, ml: 1, marginTop: `${(HEIGHT - 40) / 2}px` }}>
          <Tooltip title={'Github Repo'} enterDelay={300}>
            <IconButton component="a" color="primary" href={GITHUB_REPO} target="_blank">
              <GitHubIcon fontSize="small" />
            </IconButton>
          </Tooltip>
        </Box>
      </Container>
    </Box>
  );
} as React.FC<AppHeaderProps>);
